.error-container{

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    p{
        color:#FF6060;
    }
    
    .visual{   
        font-size: 288px;
        font-weight: 700;
        margin: 0;
        margin-top: 120px;
    }
    
    .error-description{
        font-size: 36px;
        font-weight: 500;
        margin-bottom: 120px;
    }

    .home-link{
        text-decoration: underline;
        font-size: 18px;
        font-weight: 500;
        text-underline-offset: 3px;
        color: black;
        margin-bottom: 120px;
    }
    
}



@media screen and (max-width: 768px) {

    .error-container .visual{
        font-size: 96px;
        margin-top: 60px;
    }

    .error-container .error-description{
        font-size: 18px;
        width: 60%;
        text-align: center;
    
    }

    .error-container .home-link{
        font-size: 14px;
        margin-bottom: 80px;
    }
}


