.footer{
    background-color: black;
    width: 1440px;
    height: 209px;
    margin: auto;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    img{
        width: 122px;
        padding-top: 50px;
    }
    p{
        color: white;
        font-size: 24px;
    }
}


@media screen and (max-width: 768px) {

    .footer{
        margin-top: 20px;
    }

    .footer p{
        font-size: 12px;
        width: 30%;
        text-align: center;
       
    }

}