.dropdown-container{
    margin: auto;
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    height: auto;
    background-color: white;
}



@media screen and (max-width: 768px) {

    .dropdown-container{
    
        max-width: calc(100% - 40px);
        margin: auto;
        margin-top: 20px;
        margin-bottom: 0;
    }

}